$(document).ready(function () {
    var preloader    = $('#preloader'), // селектор прелоадера
        imagesCount  = $('img').length, // количество изображений
        dBody        = $('body'), //обращаемся к body
        percent      = 100 / imagesCount, // количество % на одну картинку
        progress     = 0, // точка отсчета
        imgSum       = 5, // количество картинок
        loadedImg    = 0; // счетчик загрузки картинок

    if (imagesCount >= imgSum && imagesCount > 0) {
        preloader.css('background', '#000');
        dBody.css('overflow', 'hidden');

        $(".dws-progress-bar").circularProgress({
            color: "#25ffe4",
            line_width: 17,
            height: "350px",
            width: "350px",
            percent: 0,
            // counter_clockwise: true,
            starting_position: 25
        }).circularProgress('animate', percent, 1000);

        for (var i = 0; i < imagesCount; i++) { // создаем клоны изображений
            var img_copy        = new Image();
            img_copy.src        = document.images[i].src;
            img_copy.onload     = img_load;
            img_copy.onerror    = img_load;
        }

        function img_load () {
            progress += percent;
            loadedImg++;
            if (progress >= 100 || loadedImg == imagesCount) {
                preloader.delay(400).fadeOut('slow');
                dBody.css('overflow', '');
            }
            $(".dws-progress-bar").circularProgress('animate', progress, 500);
        }
    } else {
        preloader.remove();
    }


});

$(document).ready(function() {	

	$('.logo-line').slick({
		slidesToShow: 6,
		slidesToScroll: 1,
		lidesToShow: 3,
        autoplay: true,
        autoplaySpeed: 0,
        speed: 5000,
        cssEase:'linear',
        infinite: true,
        prevArrow: "",
        nextArrow: "",
        responsive: [
		    {
		      breakpoint: 600,
		      settings: {
		        slidesToShow: 2,
		        slidesToScroll: 2
		      }
		    }
	    ]
	});

    var cursor = $(".cursor");
    var cursorDot = $(".cursor-dot");

    $(window).mousemove(function(e) {
        cursor.css({
            top: e.clientY - cursor.height() / 2,
            left: e.clientX - cursor.width() / 2
        });
    });

    $(window)
        .mouseleave(function() {
            cursor.css({
                opacity: "0"
            });
        })
        .mouseenter(function() {
            cursor.css({
                opacity: "1"
            });
        });

    $(window)
        .mousedown(function() {
            cursor.css({
                transform: "scale(1.6)"
            });
        })
        .mouseup(function() {
            cursor.css({
                transform: "scale(1)"
            });
        });

    $('p, span, h1, h2, h3, h5, h6, ul.item__list')
        .mouseover(function() {
            cursor.css({
                transform: "scale(2)",
                background: "#fff"
            });
        })
        .mouseleave(function() {
            cursor.css({
                transform: "scale(1)",
                background: "transparent"
            });
        });

    $("a, button, .fp-controlArrow, .fp-prev, .fp-next")
        .mouseenter(function() {
            cursor.addClass("cursor-onhover")
        })
        .mouseleave(function() {
            cursor.removeClass("cursor-onhover");
        });

    $('.hamburher').on('click', function(){
    	$(this).toggleClass('active');
    	$('.mobile-nav').toggleClass('is-open');
    	$('.overlay').toggleClass('overlay-active');
    });

    $('.mobile-nav__link, .overlay').on('click', function(){
    	$('.hamburher').removeClass('active');
    	$('.mobile-nav').removeClass('is-open');
    	$('.overlay').removeClass('overlay-active');
    });
});